/* color mode switcher */
html {
  --cm-color: #232429;
  --cm-bg: #e4e4e7;
  --cm-focus: transparent /* rgba(92 173 214 / 70%) */;
}

html.dark {
  --cm-color: #c9c9cf;
  --cm-bg: #37393f;
  --cm-focus: transparent /* rgba(0 136 204 / 70%) */;
}

.color-switcher {
  position: absolute;
  top: 25px;
  right: 20px;
  margin: 0;
  padding: 0;
  line-height: 0.8;
}

.color-switcher button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
  background: transparent;
  border: 0;
  border-radius: 6px;
  box-shadow: none;
  cursor: pointer;
  transition: 250ms;
}

.color-switcher button:hover {
  background: var(--cm-bg);
}

.color-switcher button:focus,
.color-switcher button:focus-visible {
  outline: 0;
  box-shadow: 0 0 0 2px var(--cm-focus);
}

.color-switcher button svg {
  display: block;
  width: 20px;
  height: 20px;
  margin: 0;
  padding: 0;
  fill: var(--cm-color);
}

@media (width >= 480px) {
  .color-switcher {
    right: calc(50% - 240px + 20px);
  }
}
@media (width >= 768px) {
  .color-switcher {
    right: calc(50% - 384px + 20px);
  }
}
@media (width >= 1024px) {
  .color-switcher {
    right: calc(50% - 512px + 20px);
  }
}
@media (width >= 1280px) {
  .color-switcher {
    right: calc(50% - 640px + 20px);
  }
}
/* hidden class */
.hidden {
  display: none !important;
}

/* custom code */
.btn {
  display: inline-flex;
  align-items: center;
  gap: 0 4px;
  padding: 8px 24px;
  border: 1px solid var(--links);
  border-radius: 6px;
  transition: 200ms ease;
}
.btn:hover {
  text-decoration: none;
  color: white;
  background-color: var(--links);
}